<script>
  import { getLatestTransactions, getTransactionsTotal, getTransactionsSum, getUnpaidTransactions, getTransactionsPendingSum } from '../../api'

  export default {
    name: 'dash',

    data () {
      return {
        latestTransactions: [],
        transactionTotal: [],
        transactionSum: [],
        unpaidTransactions: [],
        transactionPendingSum: []
      }
    },
    methods: {
      getLatestTransactions () {
        getLatestTransactions({}).then((res) => {
          this.latestTransactions = res.data.data
        }).catch((e) => {
          // TODO
          console.log(e.message)
        })
      },
      getUnpaidTransactions () {
        getUnpaidTransactions({}).then((res) => {
          this.unpaidTransactions = res.data.data
        }).catch((e) => {
          // TODO
          console.log(e.message)
        })
      },
      getTransactionsTotal () {
        getTransactionsTotal({}).then((res) => {
          this.transactionTotal = res.data.data
        }).catch((e) => {
          //
          console.log(e.message)
        })
      },
      getTransactionsSum () {
        getTransactionsSum({}).then((res) => {
          this.transactionSum = res.data.data
        }).catch((e) => {
          //
          console.log(e.message)
        })
      },
      getTransactionsPendingSum () {
        getTransactionsPendingSum({}).then((res) => {
          this.transactionPendingSum = res.data.data
        }).catch((e) => {
        //
          console.log(e.message)
        })
      }
    },
    mounted () {
      this.getLatestTransactions()
      this.getTransactionsTotal()
      this.getTransactionsSum()
      this.getUnpaidTransactions()
      this.getTransactionsPendingSum()
    }
  }
</script>



