<template>
    <div class="card">
        <div class="card-header">
            <div class="card-actions">
                <!--<a href="#"><i class="fa fa-plus"></i> Add</a>-->

                <a href="#">
                    <small class="text-muted"></small>
                    <i class="fa fa-refresh fa-spin"></i>
                </a>
            </div>
            <i class="fa fla-align-justify"></i> Transactions
        </div>
        <div class="card-block">
            <table class="table table-striped">
                <thead>
                <tr>
                    <th>Customer</th>
                    <th>Invoice no </th>
                    <th>Amount</th>
                    <th>Date</th>
                    <th>Status</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="transaction in transactions">
                    <td>{{ transaction.first_name}} {{ transaction.last_name}}</td>
                    <td>{{ transaction.invoice_id }}</td>
                    <td>{{ transaction.amount | formatMoney('Kshs. ')}}</td>
                    <td>{{ transaction.created_at}}</td>
                    <td>
                        <span class="badge badge-success" v-if="transaction.status==1">
                        Completed
                        </span>
                        <span class="badge badge-danger" v-else>
                        Pending
                        </span>
                    </td>
                </tr>
                </tbody>
            </table>
            <ul class="pagination" v-if="false">
                <li class="page-item"><a class="page-link" href="#">Prev</a></li>
                <li class="page-item active">
                    <a class="page-link" href="#">1</a>
                </li>
                <li class="page-item"><a class="page-link" href="#">2</a></li>
                <li class="page-item"><a class="page-link" href="#">3</a></li>
                <li class="page-item"><a class="page-link" href="#">4</a></li>
                <li class="page-item"><a class="page-link" href="#">Next</a></li>
            </ul>
        </div>
    </div>
</template>

<script>
import { getTransactions } from '../../api'
export default {
  name: 'transactions',
  data () {
    return {
      transactions: []
    }
  },
  methods: {
    getTransactions () {
      getTransactions({}).then((res) => {
        console.log(res.data)
        this.transactions = res.data.data
      }).catch((e) => {
       // TODO
        console.log(e.message)
      })
    }
  },
  mounted () {
    this.getTransactions()
  }
}
</script>



