import Vue from 'vue'
import Router from 'vue-router'

// Containers
import Full from '@/containers/Full'

// Views
import Dashboard from '@/views/dashboard/Dashboard'
import Customers from '@/views/customers/Customers'
import Transactions from '@/views/transactions/Transactions'
import PaidTransactions from '@/views/transactions/PaidTransactions'
import UnpaidTransactions from '@/views/transactions/UnpaidTransactions'
import Login from '@/views/Login'
Vue.use(Router)

export default new Router({
  mode: 'hash',
  linkActiveClass: 'open active',
  scrollBehavior: () => ({ y: 0 }),
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/login',
      name: '',
      component: Login,
      hidden: false
    },
    {
      path: '/',
      name: 'Home',
      component: Full,
      children: [
        {
          path: 'dashboard',
          name: 'Dashboard',
          component: Dashboard
        }
      ]
    },
    {
      path: '/customers',
      name: 'Customers',
      component: Full,
      children: [
        {
          path: '',
          name: 'View Customers',
          component: Customers
        }
      ]
    },
    {
      path: '/transactions',
      name: 'Transactions',
      component: Full,
      children: [
        {
          path: '',
          name: 'View transactions',
          component: Transactions
        }
      ]
    },
    {
      path: '/transactions/PaidTransactions',
      name: 'Paid Transactions',
      component: Full,
      children: [
        {
          path: '',
          name: 'View transactions paid',
          component: PaidTransactions
        }
      ]
    },
    {
      path: '/transactions/UnpaidTransactions',
      name: ' Unpaid Transactions',
      component: Full,
      children: [
        {
          path: '',
          name: 'View transactions Unpaid',
          component: UnpaidTransactions
        }
      ]
    }
  ]
})



