import axios from 'axios'

let base = 'http://nouveta.tech/aplin/api/whcms-api/public/api'

export const requestLogin = params => {
  return axios.post(`${base}/login`, params).then(res => res.data)
}

export const getUserList = params => {
  return axios.get(`${base}/user/list`, {params: params})
}

export const getUserListPage = params => {
  return axios.get(`${base}/user/listpage`, {params: params})
}

export const removeUser = params => {
  return axios.get(`${base}/user/remove`, {params: params})
}

export const batchRemoveUser = params => {
  return axios.get(`${base}/user/batchremove`, {params: params})
}

export const editUser = params => {
  return axios.get(`${base}/user/edit`, {params: params})
}

export const addUser = params => {
  return axios.get(`${base}/user/add`, params)
}

// Dashboard Report
export const getLatestTransactions = params => {
  return axios.get(`${base}/transactions/latest`, params)
}
export const getTransactionsTotal = params => {
  return axios.get(`${base}/transactions/total`, params)
}
export const getTransactionsSum = params => {
  return axios.get(`${base}/transactions/sum`, params)
}
export const getTransactionsPendingSum = params => {
  return axios.get(`${base}/transactions/sumPending`, params)
}
export const getUnpaidTransactions = params => {
  return axios.get(`${base}/transactions/totalUnpaid`, params)
}
export const showdata = params => {
  return axios.get(`${base}/transactions/showdata`, params)
}
export const getPaid = params => {
  return axios.get(`${base}/transactions/paid`, params)
}
export const getUnpaid = params => {
  return axios.get(`${base}/transactions/unpaid`, params)
}

export const addTransactions = params => {
  return axios.post(`${base}/transactions`, params)
}
export const editTransaction = params => {
  return axios.put(`${base}transactions/${params.id}`, params)
}
export const deleteTransaction = params => {
  return axios.delete(`${base}/transactions/${params.id}`)
}
export const getTransactions = params => {
  return axios.get(`${base}/transactions`, params)
}
export const getTransaction = params => {
  return axios.get(`${base}/transactions/${params.id}`)
}



